:root {
  /*
    made in chrome, or webkit based browsers... (=`ω´=)
    it's basically useless in firefox (•ิ_•ิ) (tabs don't work)
    still looks nice as deco though ~

        ==== customization ====

        color scheme ! will affect the... case(?) and tab color
        can use #hex, rgb(0,0,0), colorname, etc */
  --overall-color: var(--border-color);

  /* welcome image / background ?
        i love using a png for this */
  --welcome-image: url("https://cdn.donmai.us/original/bb/9f/bb9f9d4e94d86fb9346f278de7fc8c5a.gif");

  /* custom tab titles ! */
  --tab-1-title: "about me";
  --tab-2-title: "love/hate";
  --tab-3-title: "byf";
  --tab-4-title: "dni";

  /* text that goes after every tab ! 
        use '\A' without quotes to form a line break */
  --tab-1-content: "i don't know... \A who am i?";
  --tab-2-content: "likes: girls and boys \A dislikes: kids";
  --tab-3-content: "i'm mentally insane";
  --tab-4-content: "normal people";

  /* thumbnails ! make sure you put your image
        link INSIDE the quotation marks.... please */
  --tab-1-thumbnail: url("https://images2.imgbox.com/da/68/jgYuVTnR_o.gif");
  --tab-2-thumbnail: url("https://images2.imgbox.com/bb/92/rDL6BjcM_o.gif");
  --tab-3-thumbnail: url("https://images2.imgbox.com/4a/e8/oPtxhCUI_o.gif");
  --tab-4-thumbnail: url("https://images2.imgbox.com/03/02/aP4PueaS_o.gif");
}
/* wall of code itself down below... if you know what you're doing and wanna change advanced stuff, make it readable at unminify.com */
.dantendo,
.dantendo img {
  max-width: 100%;
}
.dantendo .arrows,
.dantendo .buttons {
  filter: drop-shadow(0px 1px 1px rgba(0, 0, 0, 0.5));
}
.dantendo .arrows a,
.dantendo .buttons a {
  vertical-align: top;
  position: relative;
  display: inline-block;
}
.dantendo *,
.dantendo ::after,
.dantendo ::before {
  box-sizing: border-box;
}
.dantendo ::selection {
  background: 0 0;
}
.dantendo ::-webkit-scrollbar-thumb {
  background: url(data:image/png;base64,)
    center/contain no-repeat;
}
.dantendo ::-webkit-scrollbar-track {
  margin-inline: 2px;
}
.dantendo ::-webkit-scrollbar {
  height: 12px;
  background: url(data:image/png;base64,)
      left/contain no-repeat,
    url(data:image/png;base64,)
      right/contain no-repeat,
    url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAoAAAAWCAYAAAD5Jg1dAAAAVElEQVQ4EcWSMQ1AQQjFkI4URmQggREJuHg/OOhy+UO3pgHuzN1FsIgQwbpbBNtdEbg4MyIYkc6xqhKBb01q5zwoZqYI/K1J7Zw/i+TTnvNgRnqeD13sstPObrArAAAAAElFTkSuQmCC)
      center/contain repeat-x;
}
.dantendo ::-webkit-scrollbar:vertical {
  width: 2px !important;
  background: 0 0 !important;
}
.dantendo ::-webkit-scrollbar-thumb:vertical {
  background: rgba(0, 0, 0, 0.3) !important;
}
.dantendo .lower-screen::before {
  content: "";
  display: block;
  position: absolute;
  bottom: 6%;
  width: 30%;
  right: 0;
  left: 0;
  margin-inline: auto;
  background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACIAAAAGCAYAAACmekziAAAACXBIWXMAAFxGAABcRgEUlENBAAAAcUlEQVQokeWOIRKDQBAE+ygess9CIu8JyJGRyJVInnNyZeQ9AQeSSiUQcyJVGbXTO6LhR5IW931ZVwDGYWDMOX0a6qH9pU9KLVk3u2NmmBmz+6VxPOPtbsn6Ugp12wCocT6/ybRmnSRqBDUCSbcif5EDSLhUUXnGw2QAAAAASUVORK5CYII=)
      left/30% no-repeat,
    url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAFAAAAACCAYAAADPV1tRAAAACXBIWXMAAFxGAABcRgEUlENBAAAAJElEQVQokWNs6Gj4z8DAwNBQ0cDIAAWUiFGqf6iZycQwCigCAFqyPosK481tAAAAAElFTkSuQmCC)
      10px center/90% no-repeat;
  height: 10px;
  z-index: 9999;
  pointer-events: none;
}
.dantendo {
  display: flex;
  flex-direction: column;
  gap: 2%;
  width: 250px;
  aspect-ratio: 1/1;
  position: relative;
  font-family: "MS Pgothic";
  font-size: 80%;
  color: var(--font-main);
  text-align: center;
  margin: 5px auto;
  border-radius: 4%;
  filter: drop-shadow(0px 0px 2px rgba(0, 0, 0, 0.3));
  transition: 4s linear;
  transform-style: preserve-3d;
}
.dantendo,
.dantendo * {
  cursor: url(data:image/png;base64,),
    auto !important;
}
.dantendo .screen-division {
  background: var(--overall-color)
    linear-gradient(rgba(255, 255, 255, 0.3) 10%, transparent 90%);
  height: 50%;
  border-radius: 3%/6%;
  border: 1px solid var(--color-third);
  box-shadow: 0 0 0 3px color-mix(in lch, var(--overall-color) 80%, #fff) inset,
    0 3px 8px 2px color-mix(in lch, var(--overall-color) 60%, #000) inset;
  overflow: hidden;
}
.dantendo .screen-divider,
.dantendo .screen-divider::after,
.dantendo .screen-divider::before {
  background: var(--overall-color)
    linear-gradient(rgba(255, 255, 255, 0.5) 10%, transparent 50%);
  box-shadow: 0 -5px 5px -2px color-mix(in lch, var(--overall-color) 60%, #000)
    inset;
  position: absolute;
}
.dantendo .screen-divider {
  top: 48%;
  right: 0;
  left: 0;
  margin-inline: auto;
  width: 95%;
  height: 5%;
  border-block: 1px solid black;
}
.dantendo .screen-divider::after,
.dantendo .screen-divider::before {
  content: "";
  display: block;
  top: -20%;
  height: 135%;
  width: 14%;
  border: 1px solid #000;
  border-radius: 15%/30%;
}
.dantendo .screen-divider::before {
  left: -3%;
  background-image: linear-gradient(gray, #000), linear-gradient(gray, #000),
    linear-gradient(gray, #000);
  background-position: 30% 30%, center 30%, 70% 30%;
  background-repeat: no-repeat;
  background-size: 8% 60%;
}
.dantendo .screen-divider::after {
  right: -3%;
}
.dantendo .screen {
  background: linear-gradient(
    0deg,
    #fff 0,
    #fff 30%,
    #d7d7d7 30%,
    #d7d7d7 70%,
    #fff 70%
  );
  background-size: 100% 3px;
  border: 1px solid #000;
  outline: rgba(0, 0, 0, 0.4) solid 1px;
  outline-offset: 4px;
  box-shadow: 0 0 0 5px color-mix(in lch, var(--overall-color) 90%, gray);
  border-radius: 3%/6%;
  margin-top: calc(3% + 8px);
  margin-inline: auto;
  height: calc(85% - 9px);
  width: 45%;
  position: relative;
}
.dantendo-tab {
  transition: 0.6s 0.2s;
}
.dantendo .upper-screen .screen {
  overflow-x: hidden;
  overflow-y: auto;
}
.dantendo .lower-screen .screen {
  white-space: nowrap;
  overflow-x: auto;
  overflow-y: hidden;
  padding-right: 10%;
}
.dantendo .upper-screen::after,
.dantendo .upper-screen::before {
  content: "";
  display: block;
  position: absolute;
  top: 36%;
  height: 1.8%;
  width: 5%;
  border-radius: 40%/80%;
  background: #000;
  filter: drop-shadow(
    -1px -1px 1px color-mix(in lch, var(--overall-color) 70%, white)
  );
}
.dantendo .upper-screen::before {
  left: 12%;
}
.dantendo .upper-screen::after {
  right: 12%;
}
.dantendo-bar {
  margin: 1px;
  height: 10px;
  padding-inline: 11px;
  text-align: justify;
  font-size: 8px;
  background: url(data:image/png;base64,)
      left 2px/11px no-repeat,
    url(data:image/png;base64,)
      right 1px/11px no-repeat;
  width: fit-content;
  white-space: nowrap;
}
.dantendo-bar span {
  color: #4682b4;
  font-size: 10px;
  vertical-align: -1px;
  margin-right: 3px;
}
.welcome-image {
  transition: 0.4s;
  content: var(--welcome-image);
  height: calc(100% - 15px);
  width: 100%;
  object-fit: cover;
  object-position: bottom;
  opacity: 1;
  position: absolute;
  right: 0;
}
.dantendo-tab {
  width: 85%;
  aspect-ratio: 1/0.78;
  margin: 2% auto;
  right: 0;
  left: 0;
  background: color-mix(in lch, var(--overall-color) 50%, #fff);
  border: 2px solid #fff;
  font-size: 10px;
  padding: 2px;
  color: color-mix(in lch, var(--overall-color) 50%, #000) !important;
  overflow-y: auto;
  word-break: break-word;
  position: absolute;
  box-shadow: 0 0 4px -1px rgba(0, 0, 0, 0.5);
}
.power-button,
.select-btn,
.start-btn {
  position: absolute;
  font-family: arial;
  font-size: 7px;
  color: #000;
  user-select: none;
  text-shadow: 0 0 0 #000;
  white-space: nowrap;
}
.dantendo .tab-content,
.dantendo .tab-title {
  margin: 0 !important;
  font-size: 11px;
  white-space: pre-wrap;
}
.dantendo .tab-content {
  text-align: left;
}
.dantendo .tab-title {
  text-transform: uppercase;
}
#tab-1 .tab-title::after {
  content: var(--tab-1-title);
}
#tab-2 .tab-title::after {
  content: var(--tab-2-title);
}
#tab-3 .tab-title::after {
  content: var(--tab-3-title);
}
#tab-4 .tab-title::after {
  content: var(--tab-4-title);
}
#tab-1 .tab-content::after {
  content: var(--tab-1-content);
}
#tab-2 .tab-content::after {
  content: var(--tab-2-content);
}
#tab-3 .tab-content::after {
  content: var(--tab-3-content);
}
#tab-4 .tab-content::after {
  content: var(--tab-4-content);
}
#tab-1-button img {
  content: var(--tab-1-thumbnail);
}
#tab-2-button img {
  content: var(--tab-2-thumbnail);
}
#tab-3-button img {
  content: var(--tab-3-thumbnail);
}
#tab-4-button img {
  content: var(--tab-4-thumbnail);
}
.dantendo .screen .thumbnail {
  background: silver;
  border: 1px solid gray;
  outline: white solid 5px;
  box-shadow: 0 0 0 6px gray, 0 6px 0 4px gray;
  border-radius: 3px;
  height: 40px;
  width: 40px;
  display: inline-block;
  margin-top: 20%;
  margin-left: 30%;
  vertical-align: middle;
}
.dantendo .screen .thumbnail img {
  min-width: 100%;
  max-height: 100%;
  min-height: 100%;
  object-fit: cover;
}
:is(#tab-1, #tab-2, #tab-3, #tab-4) {
  opacity: 0;
}
.dantendo:has(#tab-1-button:focus, .arrow-up a:focus, .button-x a:focus)
  #tab-1 {
  opacity: 1;
}
.dantendo:has(
    #tab-2-button:focus,
    .arrows-left-right a:first-child:focus,
    .buttons-y-a a:first-child:focus
  )
  #tab-2 {
  opacity: 1;
}
.dantendo:has(
    #tab-3-button:focus,
    .arrows-left-right a:last-child:focus,
    .buttons-y-a a:last-child:focus
  )
  #tab-3 {
  opacity: 1;
}
.dantendo:has(#tab-4-button:focus, .arrow-down a:focus, .button-b a:focus)
  #tab-4 {
  opacity: 1;
}
.dantendo:has(a:not(.power-button):focus) .welcome-image {
  opacity: 0;
}
.select-btn,
.start-btn {
  right: 18%;
  height: 4%;
  width: 4%;
  background: var(--overall-color);
  border-radius: 100%;
  border: 1px solid #000;
  box-shadow: -2px 2px 3px rgba(255, 255, 255, 0.2) inset,
    3px -3px 3px color-mix(in lch, var(--overall-color) 80%, #000) inset,
    1px 1px 2px rgba(0, 0, 0, 0.5);
  text-indent: 140%;
}
.start-btn {
  bottom: 10%;
}
.select-btn {
  bottom: 4%;
}
.select-btn:active,
.start-btn:active {
  box-shadow: 2px -2px 3px rgba(255, 255, 255, 0.2) inset,
    -3px 3px 3px color-mix(in lch, var(--overall-color) 80%, #000) inset;
}
.dantendo .arrows {
  width: 17%;
  height: 15%;
  position: absolute;
  top: 67%;
  left: 5%;
}
.dantendo .arrows::before {
  content: "";
  display: block;
  position: absolute;
  top: 33%;
  right: 37%;
  background: var(--overall-color);
  width: 27%;
  height: 33%;
  z-index: 69;
}
.dantendo .arrows div {
  height: 33%;
  white-space: nowrap;
}
.dantendo .arrows a {
  background: var(--overall-color);
  height: 100%;
  width: 40%;
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
.dantendo .arrow-down a,
.dantendo .arrow-up a {
  width: 30%;
}
.dantendo .arrow-up a {
  background-image: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0.3) 10%,
    transparent 80%
  );
  border: 1px solid #000;
  border-bottom: 0;
  border-radius: 2px 2px 0 0;
}
.dantendo .arrow-down a,
.dantendo .arrows-left-right a:first-child {
  border: 1px solid #000;
  box-shadow: 0 -5px 5px -5px rgba(0, 0, 0, 0.8) inset;
}
.dantendo .arrow-down a {
  background-image: linear-gradient(
    0deg,
    rgba(255, 255, 255, 0.3) 10%,
    transparent 80%
  );
  border-top: 0;
  border-radius: 0 0 2px 2px;
}
.dantendo .arrows-left-right a:first-child {
  background-image: linear-gradient(
    90deg,
    rgba(255, 255, 255, 0.3) 10%,
    transparent 80%
  );
  border-right: 0px;
  border-radius: 2px 0 0 2px;
}
.dantendo .arrows-left-right a:last-child {
  background-image: linear-gradient(
    -90deg,
    rgba(255, 255, 255, 0.3) 10%,
    transparent 80%
  );
  box-shadow: 0 -5px 5px -5px rgba(0, 0, 0, 0.8) inset;
  border: 1px solid #000;
  border-left: 0;
  border-radius: 0 2px 2px 0;
}
.dantendo .arrow-up a:active {
  background-image: linear-gradient(
    180deg,
    rgba(0, 0, 0, 0.2) 10%,
    transparent 80%
  );
}
.dantendo .arrow-down a:active {
  background-image: linear-gradient(
    0deg,
    rgba(0, 0, 0, 0.2) 10%,
    transparent 80%
  );
}
.dantendo .arrows-left-right a:first-child:active {
  background-image: linear-gradient(
    90deg,
    rgba(0, 0, 0, 0.2) 10%,
    transparent 80%
  );
}
.dantendo .arrows-left-right a:last-child:active {
  background-image: linear-gradient(
    -90deg,
    rgba(0, 0, 0, 0.2) 10%,
    transparent 80%
  );
}
.dantendo .arrow-down a::after,
.dantendo .arrow-up a::after,
.dantendo .arrows-left-right a::after {
  content: "";
  display: block;
  position: absolute;
  background: #000;
  left: 0;
  right: 0;
  margin: auto;
}
.dantendo .arrows-left-right a::after {
  width: 40%;
  top: 45%;
  height: 9%;
}
.dantendo .arrow-down a::after,
.dantendo .arrow-up a::after {
  width: 9%;
  top: 25%;
  height: 50%;
}
.dantendo .buttons {
  width: 20%;
  height: 19%;
  position: absolute;
  top: 63%;
  right: 3%;
  white-space: nowrap;
}
.dantendo .buttons div {
  height: 33%;
}
.dantendo .buttons a {
  background: var(--overall-color);
  height: 100%;
  width: 33%;
  border-radius: 50%;
  border: 1px solid #000;
  background-position: center center;
  background-repeat: no-repeat;
  box-shadow: -2px -2px 5px -3px rgba(0, 0, 0, 0.6) inset,
    2px 2px 5px -2px rgba(255, 255, 255, 0.8) inset;
}
.dantendo .buttons a:active {
  box-shadow: 2px 2px 5px -3px rgba(0, 0, 0, 0.6) inset,
    -2px -2px 5px -2px rgba(255, 255, 255, 0.8) inset;
}
.dantendo .buttons .buttons-y-a a:first-child {
  margin-right: 20%;
  background-image: url(data:image/png;base64,);
  background-size: 60%;
}
.dantendo .buttons .button-x a {
  background-image: url(data:image/png;base64,);
  background-size: 115%;
}
.dantendo .buttons .button-b a {
  background-image: url(data:image/png;base64,);
  background-size: 90%;
}
.dantendo .buttons .buttons-y-a a:last-child {
  background-image: url(data:image/png;base64,);
  background-size: 75%;
}
.dantendo .camera-thingy {
  height: 100%;
  background: radial-gradient(black 50%, transparent 60%),
    radial-gradient(black 50%, transparent 60%);
  background-position: right, left;
  background-size: 20% 40%, 30% 50%;
  background-repeat: no-repeat;
  width: 8%;
  margin: auto;
  filter: drop-shadow(1px 1px rgba(255, 255, 255, 0.7));
}
.power-button:hover {
  color: black;
  text-decoration: none;
}
.power-button {
  bottom: 4%;
  left: 19%;
  height: 5%;
  width: 5%;
  background: var(--overall-color)
    url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-size: 75%;
  background-position: center center;
  border-radius: 100%;
  border: 1px solid #000;
  box-shadow: -2px 2px 3px rgba(255, 255, 255, 0.2) inset,
    3px -3px 3px rgba(0, 0, 0, 0.4) inset, 1px 1px 2px rgba(0, 0, 0, 0.5);
  text-indent: -450%;
  text-decoration: none;
}
.power-button:active {
  box-shadow: 2px -2px 3px rgba(255, 255, 255, 0.2) inset,
    -3px 3px 3px rgba(0, 0, 0, 0.4) inset, 1px 1px 2px rgba(0, 0, 0, 0.5);
}
.dantendo:has(.power-button:focus) {
  transform: perspective(200px) rotateX(80deg) rotateY(-80deg) scale(0);
}
